.inner{
    line-height: 1.5;
    display: table;
    margin: 0 auto;
    width: 200px;
    justify-content: center;
    display: inherit;
    flex-wrap: wrap;
    max-width: 100%;
    margin: 0;
    padding: 0;

}

.greybutton{
    background-color: transparent;
    border-color: white;
    border-radius: 4px;
    color: grey;
    display: block;
    padding: 16px 30px;
    font-weight: 300;
}

.blackbutton{
    font-weight: 300;
    padding: 16px 30px;
    background-color: black;
    border-color: white;
    border-radius: 4px;
    color: white;
    display: block;
    
}

.fire{
    margin: 10px;
    background-color: rgb(0, 0, 0) !important;
    border-radius: 5px !important;
    border: 0;
    position: relative;
    display: block;
    max-width: 100%;
    padding: 0;
    font: inherit;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    text-decoration: none;
    background: none;
    color: #fff;
    -webkit-user-drag: none;
}

.instaspan{
    
    display: block;
    box-sizing: border-box;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    text-overflow: ellipsis;
    background-color: hsla(0,0%,100%,0);
    border-color: transparent !important;
    padding: 16px 30px;
    border: 1px solid;
    border-radius: inherit;
    line-height: 1;
    transition: background-color .2s ease-in-out;
    margin: 0;
}

.whatsappspan{
    float: inline-start;
    display: block;
    box-sizing: border-box;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    text-overflow: ellipsis;
    background-color: (0,0%,100%,0);
    border-color: transparent !important;
    padding: 16px 30px;
    border: 1px solid;
    border-radius: inherit;
    line-height: 1;
    transition: background-color .2s ease-in-out;
    margin: 0;
}